.sidebar{
    flex: 1;
    border-right: 0.5px solid rgb(230,227,227);
    min-height: 100vh;
    background-color: white;

    .top{
        height: 50px;
        display: flex;
        align-items: center;
        justify-content: center;

        .logo{
            font-size: 20px;
            font-weight: 100;
            color: rgba(33, 33, 107, 0.545);
        }
        hr{
            height: 0;
            border: 0.5px solid rgb(233, 232, 232);
        }
    }
        
    .center{
        padding-left: 10px;

        .title{
            font-size: 10px;
            font-weight: bold;
            color: #999;
            margin-top: 15px;
            margin-bottom: 5px;
        }
        ul{
            list-style: None;
            margin: 0;
            padding: 0;

            li{
                display: flex;
                align-items: center;
                padding: 5px;
                cursor: pointer;

                .icon{
                    font-size: 18px;
                    color: #7451f8;
                }
                span{
                    font-size: 13px;
                    font-weight: 500;
                    color: #888;
                    margin-left: 10px;    

                    .link{
                        text-decoration: none;
                    }
                }
                &:hover{
                    background-color: #ece8ff;
                }
                
            }               
        }
        
    }
    .bottom{
        display: flex;
        align-items: center;
        margin: 10px;

        .colorOption{
            width: 20px;
            height: 20px;
            border-radius: 5px;
            border: 1px solid #7451f8;
            cursor: pointer;
            margin: 5px;

            &:nth-child(1){
                background-color: whitesmoke;
            }
            &:nth-child(2){
                background-color: #333;
            }
        }        
    }    
}




